import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { useParams, useNavigate } from 'react-router-dom';
import { Box, Button, InputLabel, TextField, Typography } from '@mui/material';
import toast from 'react-hot-toast';

const BlogDetails = () => {
  const [blog, setBlog] = useState({});
  const { id } = useParams();
  const navigate = useNavigate();
  const [inputs, setInputs] = useState({
    title: '',
    description: '',
    image: '',
  });

  const getBlogDetails = async () => {
    try {
      const { data } = await axios.get(`/api/v1/blog/get-blog/${id}`);
      if (data?.success) {
        setBlog(data?.blog);
        setInputs({
          title: data?.blog.title,
          description: data?.blog.description,
          image: data?.blog.image,
        });
      }
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    getBlogDetails();
  }, [id]);

  const handleChange = (e) => {
    setInputs((prevState) => ({
      ...prevState,
      [e.target.name]: e.target.value,
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const { data } = await axios.put(`/api/v1/blog/update-blog/${id}`, {
        title: inputs.title,
        description: inputs.description,
        image: inputs.image,
      });
      if (data?.success) {
        toast.success('Blog Updated');
        navigate('/my-blogs');
      }
    } catch (error) {
      console.log(error);
    }
  };

  return (
    <>
      <form onSubmit={handleSubmit}>
        <Box
          width={{ xs: '90%', sm: '80%', md: '70%', lg: '50%' }} 
          border={3}
          borderRadius={10}
          padding={3}
          margin="auto"
          boxShadow="10px 10px 20px #ccc"
          display="flex"
          flexDirection="column"
          marginTop="30px"
        >
          <Typography
            variant="h4"
            textAlign="center"
            fontWeight="bold"
            padding={3}
            color="gray"
          >
            Edit Post
          </Typography>
          <InputLabel
            sx={{ mb: 1, mt: 2, fontSize: '18px', fontWeight: 'bold' }} 
          >
            Title
          </InputLabel>
          <TextField
            name="title"
            value={inputs.title}
            onChange={handleChange}
            margin="normal"
            variant="outlined"
            required
            fullWidth
          />
          <InputLabel
            sx={{ mb: 1, mt: 2, fontSize: '18px', fontWeight: 'bold' }} 
          >
            Description
          </InputLabel>
          <TextField
            name="description"
            value={inputs.description}
            onChange={handleChange}
            margin="normal"
            variant="outlined"
            required
            multiline
            rows={4} 
            fullWidth
          />
          <InputLabel
            sx={{ mb: 1, mt: 2, fontSize: '18px', fontWeight: 'bold' }} 
          >
            Image URL
          </InputLabel>
          <TextField
            name="image"
            value={inputs.image}
            onChange={handleChange}
            margin="normal"
            variant="outlined"
            required
            fullWidth
          />
          <Button
            type="submit"
            color="warning"
            variant="contained"
            sx={{ mt: 2, padding: 1, fontSize: '16px' }} 
          >
            UPDATE
          </Button>
        </Box>
      </form>
    </>
  );
};

export default BlogDetails;
