import React, { useState } from 'react';
import { Box, AppBar, Toolbar, Button, Typography, Tabs, Tab, IconButton, Drawer, List, ListItem, ListItemText } from '@mui/material';
import { Link, useNavigate } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';
import { authActions } from '../redux/store';
import toast from 'react-hot-toast';
import MenuIcon from '@mui/icons-material/Menu';

const Header = () => {
  let isLogin = useSelector((state) => state.isLogin);
  isLogin = isLogin || localStorage.getItem('userId');
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const [value, setValue] = useState();
  const [drawerOpen, setDrawerOpen] = useState(false);

  const handleLogout = () => {
    try {
      dispatch(authActions.logout());
      toast.success('Logged out successfully!');
      navigate('/login');
      localStorage.clear();
    } catch (error) {
      console.log(error);
    }
  };

  const toggleDrawer = () => {
    setDrawerOpen(!drawerOpen);
  };

  return (
    <>
      <AppBar position="sticky">
        <Toolbar>
          <Typography variant="h4">WriteDown</Typography>
          <Box display={{ xs: 'none', md: 'flex' }} marginLeft={'auto'} marginRight={'auto'}>
            {isLogin && (
              <Tabs textColor="inherit" value={value} onChange={(e, val) => setValue(val)}>
                <Tab label="Blogs" LinkComponent={Link} to="/blogs" />
                <Tab label="My Blogs" LinkComponent={Link} to="/my-blogs" />
                <Tab label="Create Blog" LinkComponent={Link} to="/create-blog" />
              </Tabs>
            )}
          </Box>
          <Box display={{ xs: 'none', md: 'flex' }} marginLeft="auto">
            {!isLogin ? (
              <>
                <Button sx={{ margin: 1, color: 'white' }} LinkComponent={Link} to="/login">Login</Button>
                <Button sx={{ margin: 1, color: 'white' }} LinkComponent={Link} to="/register">Register</Button>
              </>
            ) : (
              <Button onClick={handleLogout} sx={{ margin: 1, color: 'white' }}>Logout</Button>
            )}
          </Box>
          <IconButton
            color="inherit"
            edge="end"
            onClick={toggleDrawer}
            sx={{ display: { xs: 'block', md: 'none' }, marginLeft: 'auto' }}
          >
            <MenuIcon />
          </IconButton>
        </Toolbar>
      </AppBar>
      <Drawer anchor="right" open={drawerOpen} onClose={toggleDrawer}>
        <Box width={250} role="presentation" onClick={toggleDrawer} onKeyDown={toggleDrawer}>
          <List>
            {isLogin && (
              <>
                <ListItem button component={Link} to="/blogs">
                  <ListItemText primary="Blogs" />
                </ListItem>
                <ListItem button component={Link} to="/my-blogs">
                  <ListItemText primary="My Blogs" />
                </ListItem>
                <ListItem button component={Link} to="/create-blog">
                  <ListItemText primary="Create Blog" />
                </ListItem>
                <ListItem button onClick={handleLogout}>
                  <ListItemText primary="Logout" />
                </ListItem>
              </>
            )}
            {!isLogin && (
              <>
                <ListItem button component={Link} to="/login">
                  <ListItemText primary="Login" />
                </ListItem>
                <ListItem button component={Link} to="/register">
                  <ListItemText primary="Register" />
                </ListItem>
              </>
            )}
          </List>
        </Box>
      </Drawer>
    </>
  );
};

export default Header;
