import * as React from 'react';
import Card from '@mui/material/Card';
import CardHeader from '@mui/material/CardHeader';
import CardMedia from '@mui/material/CardMedia';
import CardContent from '@mui/material/CardContent';
import Avatar from '@mui/material/Avatar';
import Typography from '@mui/material/Typography';
import { red } from '@mui/material/colors';
import ModeEditIcon from '@mui/icons-material/ModeEdit';
import DeleteIcon from '@mui/icons-material/Delete';
import { Box, IconButton } from '@mui/material';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import toast from 'react-hot-toast';

export default function BlogCard({ title, description, image, username, time, id, isUser }) {
  const navigate = useNavigate();

  const handleEdit = () => {
    navigate(`/blog-details/${id}`);
  };

  const handleDelete = async () => {
    try {
      const { data } = await axios.delete(`api/v1/blog/delete-blog/${id}`);
      if (data?.success) {
        toast.success('Blog Deleted');
        window.location.reload();
      }
    } catch (error) {
      console.log(error);
    }
  };

  return (
    <Card
      sx={{
        width: { xs: '100%', sm: '80%', md: '60%', lg: '50%' }, // Adjust width for different screen sizes
        margin: 'auto',
        mt: 2,
        padding: 2,
        boxShadow: '5px 5px 10px #ccc',
        ":hover": {
          boxShadow: "10px 10px 20px #ccc"
        },
        transition: 'box-shadow 0.3s ease-in-out', // Smooth transition for the shadow effect
      }}
    >
      {isUser && (
        <Box display={'flex'}>
          <IconButton onClick={handleEdit} sx={{ marginLeft: 'auto' }}>
            <ModeEditIcon color='info' />
          </IconButton>
          <IconButton onClick={handleDelete}>
            <DeleteIcon color='error' />
          </IconButton>
        </Box>
      )}
      <CardHeader
        avatar={
          <Avatar sx={{ bgcolor: red[500] }} aria-label="recipe">
            {username ? username.charAt(0).toUpperCase() : "ME"}  {/* Display the first letter of the username */}
          </Avatar>
        }
        title={username || 'Me'}
        subheader={new Date(time).toLocaleDateString()} // Format the date
      />
      <CardContent>
        <Typography variant="h6" color="text.primary">
          Title: {title}
        </Typography>
      </CardContent>
      <CardMedia
        component="img"
        height="auto" // Allow image to scale with the width of the card
        image={image}
        alt={title}
        sx={{ objectFit: 'cover' }} // Ensure the image covers the available space
      />
      <CardContent>
        <Typography variant="body2" color="text.secondary">
          Description: {description}
        </Typography>
      </CardContent>
    </Card>
  )
}

